import express from "express";
import postController from "../controller/controller";
import userController from "../controller/userscontroller";
import authentication from '../helpers/authentication';
import body from "express-validator/check";

const router = express.Router();
//post routers

router.get("/api/v1/users",authentication.adminTokenRequired,userController.getuser);
router.get("/api/v1/loans",authentication.adminTokenRequired,postController.getloans);
router.get("/api/v1/repayments", postController.getrepayments);
router.get("/api/v1/loans/notrepaid",authentication.adminTokenRequired, postController.getnotrepaid);
router.get("/api/v1/loans/repaid",authentication.adminTokenRequired,postController.getrepaid);


//post routers
router.post("/api/v1/loans",postController.createloans);
router.post("/api/v1/loans/:loanid/repayments",postController.createrepayments);
router.post("/api/v1/auth/signup", userController.createUser);
router.post("/api/v1/auth/signin", userController.login);


//get by id  routers
router.get("/api/v1/users/:id" ,authentication.adminTokenRequired,userController.getOneuser);
router.get("/api/v1/loans/:id", authentication.adminTokenRequired,postController.getOneloan);
router.get("/api/v1/loans/:id/repayments",authentication.accessTokenRequired, postController.getOnepayment);

//updates routers
//router.put("/api/v1/parties/:id", postController.updateparty);
//router.put("/api/v1/office/:id", postController.updateoffice);
//router.put("/api/v1/user/:id", postController.updateuser);


// patch router
router.patch("/api/v1/loans/:id/",authentication.adminTokenRequired,postController.verifyloan);
router.patch("/api/v1/users/:email/verify",userController.verifyuser);


//delete routers
router.delete("/api/v1/loans/:id",authentication.adminTokenRequired,postController.deleteloan);
//router.delete("/api/v1/user/:id", postController.deleteuser);
//router.delete("/api/v1/candidates/:id", postController.deletecandidate);
//router.delete("/api/v1/office/:id", postController.deleteoffice);
//router.delete("/api/v1/petition/:id", postController.deletepetition);

export default router;